@import url(https://fonts.googleapis.com/css?family=Montserrat:400,600&display=swap);
.background-fixed {
    position: fixed;
    width: 100%;
    height: 430px;
    background-image: url(https://onsurance.me/wp-content/uploads/2019/08/insurance-35.png);
    background-repeat: no-repeat;
    background-position: left top;
    z-index: 0;
}
.tipoVeiculoRadioGroup {
    width: 100%;
    text-align: center;
    font-family: 'Open Sans', sans-serif;
}

.labelFormVeiculo {
    color: #212529;
    font-size: 16px;
}

.radionGroupFormVeiculo {
    width: 100%;
    text-align: center;
    margin: 0px 0px 20px;
    font-family: 'Open Sans', sans-serif
}

.radio-button-form-veiculo {
    color: #212529 !important;
    font-weight: 600 !important;
    font-size: 16px !important;
    padding: 10px 0px !important;
    width: 100%;
    height: auto !important;
    text-align: center;
}

.radio-button-form-veiculo-checked {
    color: #01B7FF !important;
    font-weight: 600 !important;
    font-size: 16px !important;
    border: 1px solid #01B7FF !important;
    padding: 10px 0px !important;
    width: 100%;
    height: auto !important;
    text-align: center;
    background: rgba(1,183,255,.1) !important;
}

.questionH2 {
    text-align: center;
    margin-bottom: 20px;
    font-weight: 600;
    font-size: 24px;
    font-family: 'Montserrat', sans-serif;
}
.paragrafoFormCotacao {
    font-size: 16px;
    font-family: 'Open Sans', sans-serif;
    color: #21253A;
}
.jumbotronParagraph {
    margin: 0 0 20px 0;
    font-size: 16px;
    color: #21253A;
    font-family: 'Open Sans', sans-serif;
    font-weight: 400;
}

.paragraph-font-properties {
    color: #212529;
    font-size: 16px;
    font-family: 'Open Sans', sans-serif;
}
.label-form-sobre {
    color: #212529;
    font-size: 16px;
    font-family: 'Open Sans', sans-serif;
}

.jumbotron-paragraph-form-sobre {
    margin: 0px;
    font-size: 16px;
    color: #21253A;
    font-family: 'Open Sans', sans-serif;
    font-weight: 400;
}
.resultado-cotacao-dado {
    color: rgb(24, 144, 255, 0.7);
    font-weight: 600;
    font-size: 27.2px;
    text-align: center;
}

.resultado-cotacao-label {
    color: rgb(24, 144, 255, 0.7);
    font-size: 16px;
    text-align: center;
}

.resultado-cotacao-h2 {
    font-weight: 600;
    margin-top: 50px;
    margin-bottom: 40px;
    text-align: center;
    font-size: 24px;
}
.radio-button-form-onsurance {
    padding: 10px !important;
    height: auto !important;
    text-align: center;
    font-size: 18px !important;
    font-family: 'Open Sans', sans-serif !important;
    border-radius: 5px !important;
}

.radio-button-form-onsurance-checked {
    padding: 10px !important;
    height: auto !important;
    text-align: center;
    font-size: 18px !important;
    font-family: 'Open Sans', sans-serif !important;
    border-radius: 5px !important;
    background: rgba(1,183,255,.1) !important;
    color: #01B7FF;
}
.radio-button-veiculo {
    height: auto !important;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    align-items: center;
    padding: 5px 15px !important;
}

.radio-button-veiculo-checked {
    height: auto !important;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    align-items: center;
    background: rgba(1,183,255,.1) !important;
    padding: 5px 15px !important;
}

.div-label-radio-veiculo {
    font-weight: 400;
    color: #212529;
    font-size: 16px;
}

.div-label-radio-veiculo-checked {
    font-weight: 400;
    color: #01B7FF;
    font-size: 16px;
}
.jumbotrom-onsurance {
    padding: 20px;
    background-color: #F7F8FC;
    margin-bottom: 24px;
    border-radius: 5px;
}
.center-div {
    display: flex;
    justify-content: center;
    align-items: center;
}

.center-div-column {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.adquira-onsurance-div {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-bottom: 30px;
    flex-direction: column;
}

.adquira-onsurance-paragraph { 
    font-family: 'Open Sans', sans-serif;
    font-size: 14.4px;
    padding: 5px;
}
.conteudo {
    width: 100%;
    padding-bottom: 30px;
}

@media screen and (min-width: 1024px) {
    .conteudo {
        width: 100%;
        max-width: 900px;
        padding-bottom: 30px;
    }
}

.centralizado {
    text-align: center;
}

.card-main {
    background-color: #fff;
    box-shadow: 0px 2px 8px -2px rgba(140,140,140,1);
    max-height: 3000px;
    width: 100%;
}

.centralizar {
    display: flex;
    justify-content: center;
}
body {
  margin: 0;
  font-family: 'Open Sans', sans-serif;
  height: 100%;
}

#root{
  height: 100%;
}

.wrapper-app {
  min-height: 100%;
  position: relative;
  background-color: #F7F8FC
}

h2 {
  font-family: 'Montserrat', sans-serif;
  color: #333;
}

.container {
  padding-top: 40px;
  display: flex;
  justify-content: center;
  min-height: 100%;
  height: 100%;
  padding-bottom: 50px;
}
